/* App.css */

.app {
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
}

.header {
  background-image: url('/public/laptop-1478822_1280.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 100px 0; /* 필요에 따라 조절하세요 */
}

.header-title {
  font-size: 50px;
  font-weight: bolder;
  margin-bottom: 20px;
}

.header-content {
  margin: 20px;
  font-size: 20px;
}
/* styles.css */
.introduction {
  background-color: #f9f9f9;
  padding: 50px 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 30px;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.about-item {
  flex: 1 1 200px;
  margin: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.about-title {
  display: flex;
  flex-direction: row;
  align-items: end;
  justify-content: end;
}

.about-item i {
  font-size: 20px;
}

.about-item span {
  font-weight: bold;
  display: block;
  margin-top: 10px;
}

.about-item p {
  margin-top: 5px;
  font-size: 0.9em;
}

/* styles.css */
.skills {
  background-color: #ffe084;
  padding: 50px 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 30px;
}

.skills-content {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
}
.skills-title {
  width: 200px;
  margin-right: 20px;
}
.skill-category {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  margin: 20px;
  text-align: center;
}

.skill-category h3 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.skill-icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  justify-content: center;
  padding: 10px;
  border-radius: 10px;
  background-color: white;
}

.skill {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.skill-icons img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin: 10px;
}

.arc-background {
  background-color: rgb(48, 48, 48);
  padding: 40px
}

.arc-title {
  color: white;
}

.arc-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 350px;
  height: 200px;
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
}

.logo {
  padding: 20px;
}

.logo img {
  max-width: 100px;
}

.content {
  padding: 20px;
  size: 20px;
  font-weight: bold;
}

h2 {
  margin-top: 0;
}

p {
  font-size: 14px;
  line-height: 1.6;
}

.project-container {
  display: flex;
  background-color: aquamarine;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

.project-title {
  color: white;
  display: flex;
}
.project-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
}

.project-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

.project-info h1 {
  margin-top: 0;
}

.project-info img {
  width: 600px;
  height: 400px;
  margin-bottom: 40px;
}

.project-info p {
  font-size: 15px;
}

.project-stats {
  flex: 1;
  margin-top: -10px;
}

.project-stats ul {
  list-style-type: none;
  padding: 0;
}

.project-stats ul li {
  margin-bottom: 10px;
}

.project-stats ul li a {
  margin-left: 5px;
}

.stats-title {
  font-weight: bold;
  width: 200px;
}

.stats-content {
  margin-top: 10px;
  margin-left: 20px;
}

.career-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background-color: #f7e7e7;
}

.career-title {

}
.career {
  border: 2px solid #333;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: white;
}

.career h2 {
  color: #333;
}

.career-details {
  display: flex;
  justify-content: space-between;
}

.career-info {
  width: 40%;
}

.career-technologies ul {
  list-style-type: none;
  padding-left: 0;
}

.career-purpose,
.career-details-list {
  margin-top: 10px;
}

